<template>
    <div>
        <v-head :name='"资产管理帐号"'></v-head>
        <div class="module-content">
            <div class="content-search">
                 <el-button type="primary" @click='add'>新增帐号</el-button>
            </div>
            <div class="accout-table">
                <el-table :data="accoutList" style="width: 100%">
                    <el-table-column prop="accountName" label="帐号名称" width="180"></el-table-column>
                    <el-table-column prop="description" label="账号描述" ></el-table-column>
                    <el-table-column prop="address" label="帐号公钥地址" :show-overflow-tooltip="true"></el-table-column>
                    <el-table-column prop="assetCount" label="资产发行数量" >
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <add-account v-if='accountDialogShow' :show='accountDialogShow' @close='accountDialogClose'></add-account>
    </div>
</template>

<script>
import headers from "@/components/header"
import {getAccountList} from "@/api/api"
import addAccount from "./dialog/addAccount"
import errorCode from "@/util/errorCode"
export default {
    name: "accout",
    components: {
        "v-head": headers,
        "add-account": addAccount
    },
    data: function(){
        return {
            accoutList: [],
            accountDialogShow: false
        }
    },
    mounted: function(){
        this.getAccouts()
    },
    methods: {
        add: function(){
            this.accountDialogShow = true
        },
        getAccouts: function(){
            getAccountList().then(res => {
                if(res.data.code === 0){
                    this.accoutList = res.data.data
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
        accountDialogClose: function(){
            this.accountDialogShow = false;
            this.getAccouts();
        }
    }
}
</script>

<style scoped>
.content-search{
    padding: 20px 0;
    margin-bottom: 10px;
}
</style>



