<template>
    <div>
        <el-dialog title="添加商品" :visible.sync="dialogVisible" :before-close="modelClose" class="dialog-wrapper" width="400px">
            <el-form :model="goods" :rules="rules" ref="goodsForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="商品名称" prop='goodsName'>
                    <el-input v-model="goods.goodsName"></el-input>
                </el-form-item>
                <el-form-item label="商品价格" prop='goodsPrice'>
                    <el-input v-model="goods.goodsPrice" type="number"></el-input>
                </el-form-item>
                <el-form-item label="商品数量" prop='goodsConuts'>
                    <el-input v-model="goods.goodsConuts" type="number"></el-input>
                </el-form-item>
               <!-- <el-form-item label="商品标识" prop="goodsSymbol">
                        <el-select v-model="goods.goodsSymbol" placeholder="请选择" style="width: 250px;">
                            <el-option
                            v-for="item in symbolList"
                            :key="item"
                            :label="item"
                            :value="item">
                            </el-option>
                        </el-select>
                    </el-form-item> -->
                    <el-form-item label="资产名称" prop="assetId">
                        <el-select v-model="goods.assetId" placeholder="请选择" style="width: 250px;">
                            <el-option
                            v-for="item in assetList"
                            :key="item.assetId"
                            :label="item.assetName"
                            :value="item.assetId">
                            </el-option>
                        </el-select>
                    </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modelClose">取 消</el-button>
                <el-button type="primary" @click="submit('goodsForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {getAssetList,addGoods} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "addGoods",
    props: ['show'],
    data: function(){
        return {
            dialogVisible: this.show,
            goods: {
                goodsName: "",
                goodsPrice: 0,
                goodsConuts: 0,
                goodsSymbol: null,
                assetId: null
            },
            pageNumber: 1,
            pageSize: 10,
            assetList: [],
            symbolList: [0,1,2,3],
            rules: {
                goodsName: [
                    { required: true, message: '请填写商品名称', trigger: 'blur' },
                    {
                        min: 1,
                        max: 32,
                        message: "长度在 1 到 32 个字符",
                        trigger: "blur"
                    },
                ],
                goodsPrice: [
                   { required: true, message: '请填写商品价格', trigger: 'blur' }, 
                ],
                goodsConuts: [
                    { required: true, message: '请填写商品数量', trigger: 'blur' }, 
                ],
                goodsSymbol: [
                    { required: true, message: '请选择商品标志', trigger: 'blur' }
                ],
                assetId: [
                    { required: true, message: '请选择资产', trigger: 'blur' }
                ]
            }
                
        }
    },
    mounted: function(){
        this.getData()
    },
    methods: {
        modelClose: function(){
            this.$emit("close")
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.add()
                }else{
                    return false;
                }
            })
        },
        getData: function(){
            let data = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize
            }
            getAssetList(data).then(res => {
                if(res.data.code === 0){
                    let data = res.data.data
                    for(let i = 0; i < data.length; i++){
                        if(data[i].assetType == 'BAC001'){
                            this.assetList.push(data[i])
                        }
                    }
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        add: function(){
            let data = {
                assetId: this.goods.assetId,
                goodsName: this.goods.goodsName,
                goodsPrice: this.goods.goodsPrice,
                goodsCounts: this.goods.goodsConuts,
                goodsSymbol: 0
            }
            addGoods(data).then(res => {
                if(res.data.code === 0){
                    this.$message({
                        message: "添加成功！",
                        type: "success"
                    });
                    this.modelClose()
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        }
    }
}
</script>

<style>

</style>



