<template>
    <div>
        <el-dialog title="查询帐号余额" :visible.sync="dialogVisible" :before-close="modelClose" class="dialog-wrapper" width="400px">
            <el-form :model="balance" :rules="rules" ref="balance" label-width="100px" class="demo-ruleForm">
                <el-form-item label="帐号地址:" prop='address'>
                    <!-- <el-input v-model="accout.accountId"></el-input> -->
                    <el-input v-model="balance.address"></el-input>
                </el-form-item>
                <el-form-item label="余额：">
                    <span>{{balanceData || 0}}</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modelClose">取 消</el-button>
                <el-button type="primary" @click="submit('balance')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {getbalance} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "addAccount",
    props: ["show",'name','contractAddress'],
    data: function(){
        return {
            dialogVisible: this.show,
            balance: {
               address: null 
            },
            assetId: this.id,
            accoutData: [],
            rules: {
                address: []
            },
            balanceData: null
        }
    },
    methods: {
        searchBalance: function(){
            let data = {
                contractName: this.name,
                contractAddress: this.contractAddress,
                userAddress: this.balance.address,
            }
            getbalance(data).then(res => {
                if(res.data.code === 0){
                   
                    this.balanceData = res.data.data.balance
                }else{
                     debugger
                    this.$message({
                        message: "查询失败！",
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        modelClose: function(){
            this.$emit("close")
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.searchBalance()
                }else{
                    return false;
                }
            })
        },
    }
}
</script>

<style>

</style>



