//addAccount
export function addAccount(data) {
    return post({
        url: `${url.BAC_PROXY}/account/add`,
        method: 'post',
        data: data,
    })
}

//getAccountList
export function getAccountList() {
    console.log(url)
    return get({
        url: `${url.BAC_PROXY}/account/list`,
        method: 'get'
    })
}

//setIssue
export function setIssue(data) {
    return post({
        url: `${url.BAC_PROXY}/asset/issue`,
        method: 'post',
        data: data,
    })
}

//getAssetList
export function getAssetList(data) {
    return get({
        url: `${url.BAC_PROXY}/asset/list`,
        method: 'get',
        params: data
    })
}

//getAsset
export function getAsset(data) {
    return get({
        url: `${url.BAC_PROXY}/asset/${data}`,
        method: 'get',
    })
}


//getAdministration
export function getAdministration(data,list) {
    const params = reviseParam(data, list);
    return get({
        url: `${url.BAC_PROXY}/asset/accounts/${params.str}`,
        method: 'get',
    })
}

//addAdministration
