<template>
    <div class="header">
        <div class="header-content">
        <span class="header-title">BAC</span>
        <!-- <span v-if='cbShow' @click='revert'>
            <i class="el-icon-arrow-left"></i>
        </span> -->
            <!-- <el-popover
                ref="popover5"
                placement="top"
                width="160"
                v-model="visible2">
                <ul>
                    <li class="header-list" @click='changeType("BAC001")'>BAC001</li>
                    <li class="header-list" @click='changeType("BAC002")'>BAC002</li>
                </ul>
            </el-popover> -->
            <!-- <span v-popover:popover5 class="header-right">切换资产类型： 
            <span v-popover:popover5>{{bacType}}</span>
            </span> -->
            </div>
        <!-- <span class="header-right"></span> -->
    </div>
</template>

<script>
import router from "@/router"
import {getAccountList} from "@/api/api"
export default {
    name: "headers",
    props: ['name','show','path','id'],
    data: function(){
        return {
            data: this.name || "",
            cbShow: this.show || false,
            routePath: this.path || "",
            accoutName: "",
            bacType: localStorage.getItem("bacType") || "BAC001",
            visible2: false
            // secondData: this.levelData
        }
    },
    methods: {
        revert: function(){
            router.push({
                path: this.routePath,
                query: {
                    assetId: this.id
                }
            })
        },
        // getAccouts: function(){
        //     getAccountList().then(res => {
        //         if(res.data.code === 0){
        //             this.accoutName = res.data.data[0].accountName;
        //         }else{
        //             this.$message({
        //                 message: "查询失败！",
        //                 type: "error"
        //             });
        //         }
        //     }).catch(err => {
        //         this.$message({
        //                 message: "系统错误！",
        //                 type: "error"
        //             });
        //     })
        // },
        // changeType: function(row){
        //     this.bacType = row;
        //     localStorage.setItem("bacType",row);
        //     this.$emit("changeBacType")
        // },
    },
    // mounted: function(){
    //     this.getAccouts()
    // },
}
</script>

<style scoped>
.header{
    width: 100%;
    height: 48px;
    line-height: 48px;
    padding-left: 20px;
    /* font-size: 18px;
    font-weight: bold; */
    background: #242e42;
    color: #fff;
    box-sizing: border-box;
}
.header-title{
    font-size: 18px;
    font-weight: bold;
}
.header-right{
    float: right;
    /* padding-right: 40px; */
    cursor: pointer;
}
ul,li{
    list-style: none;
    margin: 0;
    padding: 0;
}
.header-list{
    width: 100%;
    line-height: 24px;
    text-align: center;
    
    cursor: pointer;
}
.header-list:hover{
    color: #409EFF;
}
.header-content{
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
}
</style>



