<template>
    <div style="padding: 0">
        <el-tabs type="border-card" @tab-click="handleClick">
            <el-tab-pane label="input">
                <div>
                    <div class="item">
                        <span class="label">Block Height:</span>
                        <span>{{transactionData.blockNumber || ""}}</span>
                    </div>
                    <div class="item">
                        <span class="label">From:</span>
                        <span class="input-data-from">
                            <span class="link" @click='goLink(transactionData.from)'>{{transactionData.from}}</span>
                        </span>
                    </div>
                    <div class="item">
                        <span class="label">
                            <el-tooltip class="item" effect="dark" content="部署合约，to字段为空。" placement="top-start">
                                <i class="el-icon-info"></i>
                            </el-tooltip>
                            <span>To:</span>
                        </span>
                        <span class="link" @click='goLink(transactionData.to)'>{{transactionData.to || 'null'}}</span>
                    </div>
                    <div class="item" v-if='funcData'>
                        <span class="label">function</span>
                        <span>{{funcData + "(" + abiType + ")"}}</span>
                    </div>
                    <div class="item" v-if="from && to">
                        <span class="label">Transferred:</span>
                        <span class="input-data-from">
                            from 
                            <span class="link" @click='goLink(from)'>{{from}}</span> 
                            to 
                            <span v-if='inputData.length' class="link" @click='goLink(to)'>{{to}}</span>
                            for 
                            <span v-if='inputData.length'>{{value}}</span>
                        </span>
                        
                    </div>
                    <div class="item" v-if='value'>
                        <span class="label">Value:</span>
                        <span class="input-data-from">
                            <span v-if='inputData.length'>{{value}}</span>
                           
                        </span>
                    </div>
                    <div class="item" v-if='comments'>
                        <span class="label">Comments:</span>
                        <span class="input-data-from">
                            <span v-if='inputData.length'>{{comments}}</span>
                        </span>
                    </div>
                    <div class="item">
                        <span class="label">input:</span>
                        <span class="input-data-from">
                            <span class="input-data-from-data">{{transactionData.input}}</span>
                        </span>
                    </div>
                </div>
            </el-tab-pane>
            <el-tab-pane label="event" v-if="eventLog.length > 0" @click="decodeEventClick">
                <div v-for="(item,num) in eventLog" v-if="eventSHow">
                    <div class="item">
                        <span class="label">Address :</span>
                        <span class="link" @click='goLink(item.address)'>{{item.address}}</span>
                    </div>
                    <div class="item">
                        <span class="label">Name :</span>
                        <span>{{item.eventName}}</span>
                    </div>
                    <div class="item">
                        <span class="label">Topics :</span>
                        <div style="display: inline-block;width:800px;">
                            <div v-for="(val,index) in item.topics " :key='val'>[{{index}}] {{val}}</div>
                        </div>
                    </div>
                    <div class="item">
                        <span class="label">Data :</span>
                        <div class="detail-input-content">
                            <span v-if="!item.eventDataShow" class="input-data">{{item.data}}</span>
                            <el-table class="input-data" :data="item.eventLgData" v-show="item.eventDataShow" style="display:inline-block;width:100%;">
                                <el-table-column prop="name" width="150" label="name" align="left"></el-table-column>
                                <el-table-column prop="data" label="data" align="left" :show-overflow-tooltip="true">
                                    <template slot-scope="scope">
                                        <!-- <i class="wbs-icon-copy font-12 copy-public-key" @click="copyPubilcKey(scope.row.data)" title="复制"></i> -->
                                        <span>{{scope.row.data}}</span>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </div>
                    </div>
                    <div class="item" v-show='item.eventButtonShow'>
                        <span class="label"></span>
                        <el-button @click="decodeButtonEvent(num)" type="primary">{{item.eventTitle}}</el-button>
                    </div>
                </div>
            </el-tab-pane>
        </el-tabs>

    </div>
</template>
<script>
import { getTransationDetail,getTransationReceipt } from "@/api/api";
import errcode from "@/util/errorCode";
import router from "@/router";

export default {
    name: "transactionDetail",
    props: {
        transHash: {
            type: String
        }
    },
    data: function() {
        return {
            detail: null,
            transactionData: {},
            contractList: [],
            decodeData: {},
            funcData: "",
            showDecode: false,
            buttonTitle: "还原",
            eventTitle: "还原",
            abiType: [],
            methodId: "",
            inputData: [],
            eventLog: [],
            buttonSHow: false,
            eventSHow: false,
            transactionTo: null,
            bin: "",
            createTime: null,
            eventDataShow: true,
            inputButtonShow: true,
            eventButtonShow: true,
            userList: [],
            abiList: JSON.parse(localStorage.getItem("abiList")) || [],
            from: "",
            to: "",
            value: null,
            comments: ""
        };
    },
    mounted: function() {
        // this.getContacts(this.getHashTransactionInfo);
        this.getHashTransactionInfo();
        // this.getUser();
    },
    destroyed: function() {
        localStorage.setItem("transaction", "");
    },
    methods: {
        link: function(val) {
            router.push({
                path: "/privateKeyManagement",
                query: {
                    userName: val
                }
            });
        },
        goLink: function(row){
            this.$emit("goLink",row)
        },
        copyPubilcKey: function(val) {
            if (!val) {
                this.$message({
                    type: "fail",
                    showClose: true,
                    message: "key为空，不复制。",
                    duration: 2000
                });
            } else {
                this.$copyText(val).then(e => {
                    this.$message({
                        type: "success",
                        showClose: true,
                        message: "复制成功",
                        duration: 2000
                    });
                });
            }
        },
        getHashTransactionInfo() {
            getTransationDetail(this.transHash)
                .then(res => {
                    if (res.status == 200) {
                        this.transactionData = res.data;
                        if (res.data) {
                            // this.getCreatTime(res.data.blockNumber);
                            this.getAdderss();
                            this.getMethod(res.data.input)
                        } else {
                            this.$message({
                                type: "error",
                                message: "无法查询到交易信息"
                            });
                        }
                    } else {
                        this.$message({
                            type: "error",
                            message: errcode.errCode[res.data.status].cn
                        });
                    }
                })
                .catch(err => {
                    this.$message({
                        type: "error",
                        message: "系统错误!"
                    });
                });
        },
        getMethod: function(id){
            let newId = id.substring(0, 10)
            for(let i = 0; i < this.abiList.length; i++){
                if(this.abiList[i].methodId === newId){
                    this.decodefun(id,this.abiList[i])
                }
            }   
        },
        handleClick(tab, event) {
            if (tab.label == "event") {
                this.decodeEventClick();
            }
        },
        decode: function() {
            if (this.showDecode) {
                this.buttonTitle = "还原";
                this.showDecode = false;
            } else {
                this.buttonTitle = "解码";
                this.showDecode = true;
            }
            if (this.eventDataShow) {
                this.buttonTitle = "解码";
                this.eventDataShow = false;
            } else {
                this.buttonTitle = "还原";
                this.eventDataShow = true;
            }
        },
        getAdderss: function() {
            getTransationReceipt(this.transHash)
                .then(res => {
                    if (res.status == 200) {
                        this.eventLog = res.data.logs;
                    } else {
                        this.$message({
                            type: "error",
                            message: errcode.errCode[res.data.status].cn
                        });
                    }
                })
                .catch(err => {
                    this.$message({
                        type: "error",
                        message: "系统错误！"
                    });
                });
        },
        //decodeEvent
        decodeEventClick: function() {
            for (let i = 0; i < this.eventLog.length; i++) {
                for(let index = 0; index < this.abiList.length; index++){
                    if(this.eventLog[i].topics[0] === this.abiList[index].methodId){
                        this.eventLog[i] = this.decodeEvent(this.abiList[index],this.eventLog[i])
                        setTimeout(() => {
                            this.eventSHow = true;
                        },200)
                    }
                }
            }
        },
        //decodeEventLog
        decodeEvent: function(eventData, data) {
            let web3 = new Web3(Web3.givenProvider);
            let abi = "";
            eventData.abiInfo = JSON.parse(eventData.abiInfo)
            let list = data;
                if(list.data == "0x"){
                    list.eventDataShow = false
                    list.eventButtonShow = false;
                }else{
                    list.eventDataShow = true;
                    list.eventButtonShow = true;
                }
                list.eventTitle = '还原'
                list.eventName = eventData.abiInfo.name + "(";
                for (let i = 0; i < eventData.abiInfo.inputs.length; i++) {
                    if (i == eventData.abiInfo.inputs.length - 1) {
                        list.eventName =  list.eventName + eventData.abiInfo.inputs[i].type +" " + eventData.abiInfo.inputs[i].name;
                    }else{
                        list.eventName = list.eventName + eventData.abiInfo.inputs[i].type + " " + eventData.abiInfo.inputs[i].name + ",";
                    }   
                }
                list.eventName = list.eventName + ")";
                let eventResult = null;
                try {
                    eventResult = web3.eth.abi.decodeLog(eventData.abiInfo.inputs,list.data,list.topics.slice(1));
                }catch(error) {
                    console.log(error)
                }
                list.outData = {};
                list.eventLgData = [];
                for (const key in eventResult) {
                    if (+key || +key == 0) {
                        list.outData[key] = eventResult[key];
                    }
                }
                if (eventData.abiInfo.inputs.length && JSON.stringify(list.outData) != "{}") {
                    for (const key in list.outData) {
                        eventData.abiInfo.inputs.forEach((items, index) => {
                            if (index == key) {
                                list.eventLgData[index] = {};
                                list.eventLgData[index].name = items.name;
                                list.eventLgData[index].data = list.outData[key];
                            }
                        });
                    }
                }
            return list
        },
        decodeButtonEvent: function(num){
            if(this.eventLog[num].eventDataShow){
                this.$set(this.eventLog[num],'eventDataShow',false);
                this.$set(this.eventLog[num],'eventTitle','解码')
                this.$set(this.eventLog,num,this.eventLog[num])
            }else{
                this.$set(this.eventLog[num],'eventDataShow',true);
                this.$set(this.eventLog[num],'eventTitle','还原');
                this.$set(this.eventLog,num,this.eventLog[num])
            }
        },
        //transactionDecode
        decodefun: function(input,abiData) {
            let web3 = new Web3(Web3.givenProvider);
            if (this.userList.length) {
                this.userList.forEach(value => {
                    if (value.address == this.transactionData.from) {
                        this.transactionData.user = value.userName;
                    }
                });
            }
            this.methodId = input.substring(0, 10);
            // this.methodId = data;
            let inputDatas = "0x" + input.substring(10);
            if(abiData){
                abiData.abiInfo = JSON.parse(abiData.abiInfo)
                abiData.abiInfo.inputs.forEach((val, index) => {
                    if (val && val.type && val.name) {
                        this.abiType[index] = val.type + " " + val.name;
                    } else if (val && val.name) {
                        this.abiType[index] = val.name;
                    } else if (val && val.type) {
                        this.abiType[index] = val.type;
                    } else if (val) {
                        this.abiType[index] = val;
                    }
                });
                this.funcData = abiData.abiInfo.name;
                if (abiData.abiInfo.inputs.length) {
                    this.decodeData = web3.eth.abi.decodeParameters( abiData.abiInfo.inputs,inputDatas);
                    console.log(this.decodeData)
                    if (JSON.stringify(this.decodeData) != "{}") {
                        for (const key in this.decodeData) {
                            abiData.abiInfo.inputs.forEach((val, index) => {
                                if (val && val.name && val.type) {
                                    if (key === val.name) {
                                        this.inputData[index] = {};
                                        this.inputData[index].name = val.name;
                                        this.inputData[index].type = val.type;
                                        this.inputData[index].data = this.decodeData[key];
                                    }
                                } else if (val) {
                                    if (index == key) {
                                        this.inputData[index] = {};
                                        this.inputData[index].type = val;
                                        this.inputData[index].data = this.decodeData[key];
                                    }
                                }
                            });
                        }
                    }
                }
                if(this.inputData.length){
                    for(let i = 0; i < this.inputData.length; i++){
                        if(this.inputData[i].name == 'from' || this.inputData[i].name == '_from'){
                             this.from = this.inputData[i].data;
                        }else if(this.inputData[i].name == 'to' || this.inputData[i].name == '_to'){
                             this.to = this.inputData[i].data;
                        }else if(this.inputData[i].name == 'value' || this.inputData[i].name == '_value'){
                             this.value = this.inputData[i].data;
                        }else if(this.inputData[i].name == 'data' || this.inputData[i].name == '_data'){
                             this.comments = this.inputData[i].data;
                        }
                    }
                    // if(this.inputData[0].name == "from"){
                    //     this.from = this.inputData[0].data;
                    //     this.to = this.inputData[1].data;
                    //     this.value = this.inputData[2].data;
                    //     this.comments = this.inputData[3].data;
                    // }else if(this.inputData[0].name == "to"){
                    //     this.from = this.transactionData.from;
                    //     this.to = this.inputData[0].data;
                    //     this.value = this.inputData[1].data;
                    //     this.comments = this.inputData[2].data;
                    // }
                }
            }
        },
    }
};
</script>
<style scoped>
.label {
    display: inline-block;
    width: 120px;
    font-weight: bold;
    vertical-align: top;
    font-size: 12px;
}
.item {
    padding: 8px 0;
}
.input {
    display: inline-block;
    font-size: 0;
    vertical-align: top;
}
.input span {
    font-size: 12px;
}
.input-data {
    display: inline-block;
    width: 100%;
    padding: 10px;
    max-height: 200px;
    overflow: auto;
    word-break: break-all;
    word-wrap: break-word;
    box-sizing: border-box;
}
.input-data-from {
    display: inline-block;
    width: calc(100% - 200px);
    height: auto;
    word-break: break-all;
    word-wrap: break-word;
    box-sizing: border-box;
}
.input-label {
    padding-bottom: 15px;
}
.input-label .label {
    width: 80px;
    font-size: 12px;
}
.detail-input-content {
    display: inline-block;
    width: calc(100% - 200px);
    height: auto;
    border: 1px solid #eaedf3;
    border-radius: 4px;
    font-size: 12px;
}
.input-data >>> th{
    background-color: #e8e8e8 !important;
    border-color: #e8e8e8 !important;
    color: #666 !important;
}
.input-data >>> tr{
    background-color: #e8e8e8 !important;
     border-color: #e8e8e8 !important;
     color: #666 !important;
}
.input-data >>>  td{
    background-color: #fff !important;
    border-color: #e8e8e8 !important;
     color: #666 !important;
}
/* .input-data >>>  td{
    background-color: #fff !important;
    border-color: #e8e8e8 !important;
     color: #666 !important;
} */
.input-data >>>  table{
    background-color: #e8e8e8 !important;
    border-color: #e8e8e8 !important;
      color: #666 !important;
}
.input-data >>>  tbody{
    background-color: #e8e8e8 !important;
    border-color: #e8e8e8 !important;
      color: #666 !important;
}
.detail-input-content >>>.el-table{
    /* background-color: #e8e8e8 !important; */
    border-color: #e8e8e8 !important;
      color: #666 !important;
}
.input-data >>> .el-table__body-wrapper{
    background-color: #e8e8e8 !important;
    border-color: #e8e8e8 !important;
      color: #666 !important;
}
.input-data >>>.el-table td, .el-table th.is-leaf{
    border-color: #e8e8e8 !important;
}
.link{
    color: #409EFF;
    cursor: pointer;
}
.el-table::before{
    height: 0;
}
.input-data-from-data{
    display: inline-block;
    width: 60%;
    max-width: 600px;
    padding: 5px;
    height: 80px;
    overflow: auto;
    border: 1px solid #e8e8e8;
}
</style>





