<template>
    <div>
        <el-dialog title="增发" :visible.sync="dialogVisible" :before-close="modelClose" class="dialog-wrapper" width="400px">
            <el-form :model="increase" :rules="rules" ref="increaseForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="接收者" prop='accountTo'>
                        <el-autocomplete v-model="increase.accountTo" :fetch-suggestions="querySearchAsync" style="width: 250px;"></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="数量"  v-if='assetData && assetData.assetType === "BAC001"' prop='number'>
                        <el-input v-model="increase.number" style="width: 250px;" type='number'></el-input>
                    </el-form-item>
                    <el-form-item label="物品编号"  v-if='assetData && assetData.assetType === "BAC002"' prop='tokenId'>
                        <el-input v-model="increase.tokenId" style="width: 250px;"></el-input>
                    </el-form-item>
                    <el-form-item label="物品描述"  v-if='assetData && assetData.assetType === "BAC002"' prop='tokenURI'>
                        <el-input v-model="increase.tokenURI" style="width: 250px;"></el-input>
                    </el-form-item>
                    <el-form-item >
                        <el-button type="primary" @click="submit('increaseForm')">确 定</el-button>
                    </el-form-item>
                </el-form>
        </el-dialog>
    </div>
</template>
<script>
import {increase,getAsset,getAdministration,deleteAccount,addAdministration,getAccountList} from "@/api/api"
import addAccount from "../dialog/addAccount"
import router from "@/router"
import errorCode from "@/util/errorCode"
export default {
    name: "addAsset",
    props: ['show','id'],
    data: function(){
        return {
            increase: {
                accountTo: "",
                number: "",
                tokenId: "",
                tokenURI: "",
                comments: ""
            },
            accountShow: false,
            assetData: null,
            assetId: this.id || null,
            accountList: [],
            accoutData: [],
            accountName: "",
            dialogVisible: this.show,
            // timeout: null,
            htilte: "增发",
            rules: {
               accountTo: [
                   { required: true, message: '请填写地址', trigger: 'change' },
               ],
               number: [
                  { required: true, message: '请填写数量', trigger: 'change' },
               ],
               tokenId: [
                   { required: true, message: '请填写token编号', trigger: 'change' },
               ],
               tokenURI: [
                   { required: true, message: '请填写token描述', trigger: 'change' },
               ]
            }
        }
    },
    mounted: function(){
        // this.restaurants = this.loadAll();
        this.getData(this.getadminAccounts);
        this.getAccouts()
    },
    methods: {
        getadminAccounts: function(){
            let data = {
                assetId: this.assetId,
                accountType: 0
            };
            getAdministration(data).then(res => {
                if(res.data.status === 0){
                    this.accountList = res.data.data
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        querySearchAsync(queryString, cb) {
            var restaurants = this.accoutData;
            for(let i = 0; i < restaurants.length; i++){
                restaurants[i].value = restaurants[i].address
            }
            var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 500 * Math.random());
        },
        createStateFilter(queryString) {
            return (state) => {
                return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        getAccouts: function(){
            getAccountList().then(res => {
                if(res.data.status === 0){
                    this.accoutData = res.data.data
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        getData: function(callback){
            getAsset(this.assetId).then(res => {
                if(res.data.status === 0){
                    this.assetData = res.data.data;
                    this.accountName = res.data.data.assetName;
                    // this.htilte = `${this.accountName}/增发`
                    callback()
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.increaseData()
                }else{
                    return false;
                }
            })
        },
        increaseData: function(){
            let data = {
                assetId: this.assetData.assetId,
                transFrom: this.accoutData[0].address,
                transTo: this.increase.accountTo,
                comments: this.increase.comments
            };
            if(this.assetData.assetType == 'BAC001'){
                data.amount = this.increase.number
            }else if(this.assetData.assetType == 'BAC002'){
                data.itemId = this.increase.tokenId;
                data.itemUri = this.increase.tokenURI
            }
            increase(data).then(res => {
                if(res.data.status === 0){
                    this.$message({
                        message: "增发成功！",
                        type: "success"
                    });
                    this.modelClose()
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        modelClose: function(){
            this.$emit("close")
        }
    }
}
</script>




