<template>
    <div>
        <v-head :name='htilte' :show='true' :path='"/administration"' :id='assetId'></v-head>
        <div class="module-content">
            <div class="module-admin">
                <el-form :model="transfer" :rules="rules" ref="transForm" label-width="120px" class="demo-ruleForm">
                    <el-form-item label="接收地址" prop="accountId">
                        <el-autocomplete v-model="transfer.addressTo" :fetch-suggestions="querySearchAsync" style="width: 250px;"></el-autocomplete>
                    </el-form-item>
                    <el-form-item label="数量"  v-if='assetData && assetData.assetType === "BAC001"'>
                        <el-input v-model="transfer.number" style="width: 250px;"></el-input>
                    </el-form-item>
                    <el-form-item label="物品编号"  v-if='assetData && assetData.assetType === "BAC002"'>
                        <el-input v-model="transfer.tokenId" style="width: 250px;"></el-input>
                    </el-form-item>
                    <el-form-item label="描述">
                        <el-input v-model="transfer.comments" type="textarea" style="width: 250px;"></el-input>
                    </el-form-item>
                    <el-form-item >
                        <el-button type="primary" @click="submit('transForm')">确认转账</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="admin-table">
                <div class='admin-table-search'>
                    <span class="admin-table-search-title">最近交易列表</span>
                </div>
                 <el-table :data="transationList" style="width: 100%">
                    <el-table-column prop="txHash" label="交易哈希" :show-overflow-tooltip="true" width="480px"></el-table-column>
                        <el-table-column prop="blockHeight" label="块高" :show-overflow-tooltip="true" width="80px"></el-table-column>
                        <el-table-column prop="from" label="转账地址" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column prop="to" label="接收地址" :show-overflow-tooltip="true"></el-table-column>
                        <el-table-column prop="value" label="转账额" width="100px"></el-table-column>
                        <el-table-column prop="blockTimeStamp" label="交易时间"  width="150px"></el-table-column>
                        <el-table-column prop="data" label="附加信息"  width="180px"></el-table-column>
                </el-table>
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageNumber"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="sizes, prev, pager, next"
                    :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import headers from "@/components/header"
import {getAsset,getAdministration,transfer,getTransactions,pause,getAccountList} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "transfer",
    components: {
        "v-head": headers,
    },
    data: function(){
        return {
            htilte: "转账",
            assetId: this.$route.query.assetId || null,
            transfer: {
                addressFrom: "",
                addressTo: "",
                number: 0,
                tokenId: null,
                comments: ""
            },
            rules: {
                addressFrom: [],
                addressTo: []
            },
            transationList: [],
            pageNumber:1,
            pageSize: 10,
            address: "",
            assetData: null,
            accoutData: null,
            total: 0,
            assetAddress: this.$route.query.address
        }
    },
    mounted: function(){
        this.getAccouts();
        this.getData();
        this.getTransactionList()
    },
    methods: {
        getData: function(){
            getAsset(this.assetId).then(res => {
                if(res.data.code === 0){
                    this.assetData = res.data.data;
                    if(this.assetData.assetStatus == 0){
                        this.buttonTitle = "暂停"
                    }else if(this.assetData.assetStatus == 1){
                        this.buttonTitle = "恢复"
                    }
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        getAccouts: function(){
            getAccountList().then(res => {
                if(res.data.code === 0){
                    this.accoutData = res.data.data
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        querySearchAsync(queryString, cb) {
            var restaurants = this.accoutData;
            for(let i = 0; i < restaurants.length; i++){
                restaurants[i].value = restaurants[i].address
            }
            var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 500 * Math.random());
        },
        createStateFilter(queryString) {
            return (state) => {
                return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.transfering()
                }else{
                    return false;
                }
            })
        },
        getTransactionList: function(){
             let query = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize,
                contractAddress: this.assetAddress
            }
            getTransactions({},query).then(res =>{
                if(res.data.code === 0){
                    this.transationList = res.data.data.result || [];
                    this.total = res.data.data.totalCount
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        handleSizeChange: function(){
            this.pageNumber = 1;
            this.getTransactionList();
        },
        handleCurrentChange: function(){
            this.getTransactionList();
        },
        transfering: function(){
            let data = {
                assetId: this.assetId,
                transFrom:  this.accoutData[0].address,
                transTo: this.transfer.addressTo,
                comments: this.transfer.comments,
            }
            if(this.assetData.assetType == "BAC001"){
                data.transferAmount = this.transfer.number;
            }else if(this.assetData.assetType == "BAC002"){
                data.itemId = this.transfer.tokenId
            }
            transfer(data).then(res => {
                if(res.data.code === 0){
                     this.$message({
                        message: "转账成功！",
                        type: "success"
                    });
                    this.getTransactionList();
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        }
    }
}
</script>

<style>
.admin-info{
    width: 60%;
    font-size: 0;
    margin: 0 auto;
}
</style>



