<template>
    <div style="width: 100%;box-sizing:border-box;">
        <v-head :name='"资产管理帐号"'></v-head>
        <div class="module-content">
            <div class="module-admin">
            <el-form :model="issueData" :rules="rules" ref="issueForm" label-width="150px" class="demo-ruleForm" style="width: 500px;">
                <el-form-item label="资产名称" prop="name">
                    <el-input v-model="issueData.name" style="width: 250px;"></el-input>
                </el-form-item>
                <!-- <el-form-item label="资产管理帐号选择" prop="name" width='180'>
                    <el-select v-model="issueData.account" placeholder="请选择资产管理帐号" style="width: 250px;">
                        <el-option v-for='item in accountList' :key='item.accountId' :label='item.accountName' :value='item.accountId'></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="资产类型选择" prop="type">
                    <el-select v-model="issueData.type" placeholder="请选择活动区域" style="width: 250px;">
                        <el-option label='BAC001' value='BAC001'></el-option>
                        <el-option label='BAC002' value='BAC002'></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="资产粒度" v-if='issueData.type=="BAC001"' prop='number'>
                    <el-input v-model="issueData.number" type=number  style="width: 250px;"></el-input>
                </el-form-item>
                <el-form-item label="发行总量" v-if='issueData.type=="BAC001"' prop='initialSupply'>
                    <el-input v-model="issueData.initialSupply" type=number style="width: 250px;"></el-input>
                </el-form-item>
                <!-- <el-form-item label="帐号编号" v-if='issueData.type=="BAC002"' prop='tokenId'>
                    <el-input v-model="issueData.tokenId" style="width: 250px;"></el-input>
                </el-form-item> -->
                <el-form-item label="描述">
                    <el-input v-model="issueData.description"  type="textarea" style="width: 250px;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('issueForm')">确认发行</el-button>
                </el-form-item>
            </el-form>
        </div>
        </div>
    </div>
</template>

<script>
import headers from "@/components/header"
import {setIssue,getAccountList} from "@/api/api"
import router from "@/router"
import errorCode from "@/util/errorCode"
export default {
    name: "issue",
    components: {
        "v-head": headers
    },
    data: function(){
        return {
            accountList: [],
            accountId: null,
            issueData: {
                name: "",
                account: "",
                type: "BAC001",
                number: 0,
                total: "",
                symbolType: 0,
                tokenId: null,
                description: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入合约名称",
                        trigger: "blur"
                    },
                    {
                        min: 1,
                        max: 32,
                        message: "长度在 1 到 32 个字符",
                        trigger: "blur"
                    },
                ],
                type: [
                        {
                        required: true,
                        message: "请选择资产类型",
                        trigger: "blur"
                    },
                ],
                number: [
                    {
                        required: true,
                        message: "请输入资产粒度",
                        trigger: "blur"
                    },
                ],
                initialSupply: [
                    {
                        required: true,
                        message: "请输入资产数量",
                        trigger: "blur"
                    },
                ],
                tokenId: [
                   { required: true, message: '请填写token编号', trigger: 'change' },
               ],
               tokenURI: [
                   { required: true, message: '请填写token描述', trigger: 'change' },
               ]
            }
        }
    },
    mounted: function(){
        this.getAccouts()
    },
    methods: {
        submitForm: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.issue()
                }else{
                    return false;
                }
            })
        },
        getAccouts: function(){
            getAccountList().then(res => {
                if(res.data.code === 0){
                    this.accountList = res.data.data;
                    // this.issueData.account = this.accountList[0].accountId;
                    // this.accountId = this.accountList[0].accountId
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
        issue: function(){
            let data = {
                assetName: this.issueData.name,
                assetType: this.issueData.type,
                accountId: this.accountList[0].accountId,
                description: this.issueData.description
            }
            if(data.assetType == "BAC001"){
                data.initialSupply = this.issueData.initialSupply;
                data.minUnit = this.issueData.number;
            }
            setIssue(data).then(res => {
                if(res.data.code === 0){
                    this.$message({
                        message: "发行成功！",
                        type: "success"
                    });
                    router.push({
                        path: "/home"
                    })
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
    }
}
</script>

<style scoped>

</style>



