<template>
    <div style="width: 100%">
        <div class="header">
            <span  @click='revert' style="cursor: pointer;">
                <i class="el-icon-arrow-left"></i>
                返回首页
            </span>
        </div>
        <div class="module-content">
            <div class="module-admin">
                <div class="admin-info-item">
                    <span class="admin-info-item-label">资产名称:</span>
                    <span class="admin-info-item-content">{{assetName}}</span>
                </div>
                <div class="admin-info-item" v-if='data'>
                    <span class="admin-info-item-label">发行者:</span>
                    <span class="admin-info-item-content">{{data.from}}</span>
                </div>
                <div class="admin-info-item" v-if='bacType === "BAC001"'>
                    <span class="admin-info-item-label" >初始发行总量：</span>
                    <span class="admin-info-item-content">{{assetTotal}}</span>
                </div>
                <div class="admin-info-item" v-if='contractData'>
                    <span class="admin-info-item-label">实际总量：</span>
                    <span class="admin-info-item-content">{{contractData.assetTotal}}</span>
                </div>
                <div class="admin-info-item" v-if='data'>
                    <span class="admin-info-item-label">合约地址：</span>
                    <span class="admin-info-item-content">{{data.contractAddress}}</span>
                </div>
                <div class="admin-info-item" v-if='contractData'>
                    <span class="admin-info-item-label">状态：</span>
                    <span class="admin-info-item-content">{{contractData.assetStatus | Status}}</span>
                </div>
                <div class="admin-info-item">
                    <span class="admin-info-item-label">类型：</span>
                    <span class="admin-info-item-content color-red">{{bacType}}</span>
                </div>
                <div class="admin-info-item">
                    <span class="admin-info-item-label">描述：</span>
                    <span class="admin-info-item-content">{{description}}</span>
                </div>
            </div>
            <div class="admin-table">
                <div class="admin-table-search">
                    <span class="admin-table-search-title">交易列表</span>
                    <el-input placeholder="请输入地址" v-model="address" class="input-with-select">
                        <el-button slot="append" icon="el-icon-search" @click='searchData'></el-button>
                    </el-input><br>
                    <div v-if='address' class="table-title-content">
                        <div class="table-title-content-item">
                            <span>用户地址：</span>
                            <span>{{address}}</span>
                        </div>
                        <div class="table-title-content-item">
                            <span>用户余额：</span>
                            <span>{{balanceData}}</span>
                        </div>    
                    </div>
                    
                </div>
                <div class="admin-table-contant">
                    
                    <el-table :data="transactionList" style="width: 100%" :row-key="getRowKeys" :expand-row-keys="expands" @row-click="clickTable" ref="refTable">
                        <el-table-column type="expand" align="center">
                            <template slot-scope="scope">
                                <v-transaction-detail :transHash="scope.row.txHash" @goLink='link($event)'></v-transaction-detail>
                            </template>
                        </el-table-column>
                        <el-table-column prop="txHash" label="交易哈希" :show-overflow-tooltip="true" width="480px">
                            <!-- <template slot-scope="scope">
                                <el-button @click="handleClick(scope.row.txHash)" type="text" size="small">{{scope.row.txHash}}</el-button>
                            </template> -->
                        </el-table-column>
                        <el-table-column prop="blockHeight" label="块高" :show-overflow-tooltip="true" width="80px"></el-table-column>
                        <el-table-column prop="from" label="转账地址" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                <i class="wbs-icon-copy font-12 copy-public-key" @click="copyPubilcKey(scope.row.from)" title="复制"></i>
                                <el-button @click="link(scope.row.from)" type="text" size="small" class="font-nowrap">{{scope.row.from}}</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column prop="to" label="接收地址" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                <i class="wbs-icon-copy font-12 copy-public-key" @click="copyPubilcKey(scope.row.to)" title="复制"></i>
                                <el-button @click="link(scope.row.to)" type="text" size="small" class="font-nowrap">{{scope.row.to}}</el-button>
                            </template>
                        </el-table-column>
                        <el-table-column prop="value" label="转账额" width="100px"></el-table-column>
                        <el-table-column prop="blockTimeStamp" label="交易时间"  width="150px"></el-table-column>
                        <el-table-column prop="data" label="附加信息"  width="180px"></el-table-column>
                    </el-table>
                    <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageNumber"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="sizes, prev, pager, next"
                    :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
        <add-asset :id='assetId' :show='addAssetShow' v-if='addAssetShow' @close='addAssetClose'></add-asset>
    </div>
</template>

<script>
import headers from "@/components/header"
import Bus from "@/bus"
import {getTransactions,getAsset,getAccountList,pause,getTransationDetail,getTransationReceipt, getcontarctData,getbalance} from "@/api/api"
import router from "@/router"
import constant from "@/util/constant"
import errorCode from "@/util/errorCode"
import addAsset from "./dialog/addAsset"
import balance from "./dialog/searchBalance"
import transactionDetail from "./components/transactionDetail";
export default {
    name: "administration",
    components: {
        "v-head": headers,
        "add-asset": addAsset,
        "v-transaction-detail": transactionDetail,
        "v-search-balance": balance
    },
    data: function(){
        return {
            account: "",
            htitle: "",
            data: null,
            address: "",
            transactionList: [],
            pageNumber: 1,
            pageSize: 10,
            assetId: this.$route.query.assetId,
            total: 0,
            addAssetShow: false,
            suspendShow: false,
            btitle: "暂停",
            accoutData: [],
            userAddress: "",
            expands: [],
            getRowKeys: function(row) {
                return row.pkId;
            },
            assetTotal: this.$route.query.total,
            description: this.$route.query.description,
            txHash: this.$route.query.txHash,
            assetName: this.$route.query.assetName,
            bacType: localStorage.getItem("bacType") || "BAC001",
            contractData: null,
            balanceShow: false,
            balanceData: 0
        }
    },
    mounted: function(){
        // if(localStorage.getItem("account")){
        //     this.account = JSON.parse(localStorage.getItem("account"))
        // }
        this.getAdderss()
    },
    methods: {
        copyPubilcKey: function(val) {
            if (!val) {
                this.$message({
                    type: "fail",
                    showClose: true,
                    message: "key为空，不复制。",
                    duration: 2000
                });
            } else {
                this.$copyText(val).then(e => {
                    this.$message({
                        type: "success",
                        showClose: true,
                        message: "复制成功",
                        duration: 2000
                    });
                });
            }
        },
        revert: function(){
            router.push("/home")
        },
        getContarct: function(){
            let data = {
                contractName: this.bacType,
                contractAddress: this.data.contractAddress
            }
            getcontarctData(data).then(res => {
                if(res.data.status === 0){
                    this.contractData = res.data.data
                }
            })
        },
        searchBalance: function(){
            this.balanceShow = true
        },
        balanceClose: function(){
            this.balanceShow = false
        },
        searchBalance: function(){
            let data = {
                contractName: this.bacType,
                contractAddress: this.data.contractAddress,
                userAddress: this.address,
            }
            getbalance(data).then(res => {
                if(res.data.code === 0){
                    this.balanceData = res.data.data.balance
                }else{
                    this.$message({
                        message: "查询失败！",
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        searchData: function(){
            this.userAddress = this.address,
            this.search();
            if(this.address){
                this.searchBalance();
            }
        },
        getAdderss: function() {
            getTransationReceipt(this.txHash)
                .then(res => {
                    if (res.status == 200) {
                        this.data = res.data
                        this.search();
                        this.getContarct();
                    } else {
                        this.$message({
                            type: "error",
                            message: errcode.errCode[res.data.status].cn
                        });
                    }
                })
                .catch(err => {
                    this.$message({
                        type: "error",
                        message: "系统错误！"
                    });
                });
        },
        handleClick: function(row){
            getTransationDetail(row).then(res => {
                console.log(res.data)
                if(res.data.status === 0){
                    console.log(res.data)
                }
            })
            getTransationReceipt(row).then(res => {
                console.log(res.data)
            })
        },
        link: function(row){
            this.userAddress = row;
            this.address = row
            this.search();
            if(this.address){
                this.searchBalance();
            }   
        },
        changeBacType: function(){
            this.bacType = localStorage.getItem("bacType");
            this.getList()
        },
        clickTable: function(row, column,$event) {
            let nodeName = $event.target.parentNode.nodeName;
            let newName = $event.target.nodeName;
            if (nodeName === "BUTTON" || newName === "I") {
                return
            }
            this.$refs.refTable.toggleRowExpansion(row);
        },
        addAssetClose: function(){
            this.addAssetShow = false;
            this.getData(this.assetId);
            this.search()
        },
        add: function(){
            this.addAssetShow = true;
        },
        changeStatus: function(){
            this.$confirm('确认此次操作？')
                .then(_ => {
                   this.suspend(val)
            })
            .catch(_ => {});
        },
        suspend: function(){
            let data = {
                assetId: this.assetId,
                accountId: this.accoutData[0].accountId,
            }
            if(this.data.assetStatus == 0){
                data.type = 0
            }else if(this.data.assetStatus == 1){
                data.type = 1
            }
            pause(data).then(res => {
                if(res.data.status === 0){
                    this.suspendClose()
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        suspendClose: function(){
            this.getData(this.assetId);
            this.search()
        },
        transferAccount: function(){
            router.push({
                path: "/transfer",
                query: {
                    assetId: this.data.assetId,
                    address: this.data.assetAddress
                }
            })
        },
        search: function(){
           this.transactionList = [];
           this.expands = []
            let query = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize,
                contractAddress: this.data.contractAddress
            }
            if(this.userAddress){
                query.userAddress = this.userAddress
            }
            if(this.bacType === "BAC002"){
                query.contractName = this.bacType,
                query.unitName = 'BAC002Send'
            }
            getTransactions({},query).then(res => {
                if(res.data.status === 0){
                    this.transactionList = res.data.data.result || [];
                    console.log(res.data)
                    this.total = res.data.data.totalCount;
                    this.userAddress = ""
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        handleSizeChange: function(){
            this.pageNumber = 1;
            this.search();
        },
        handleCurrentChange: function(){
            this.search();
        }
    },
    filters: {
        Status: function(value){
            return constant.STATUS[value]
        }
    }
}
</script>

<style scoped>
.header{
    width: 100%;
    height: 48px;
    line-height: 48px;
    padding-left: 20px;
    /* font-size: 18px;
    font-weight: bold; */
    background: #242e42;
    color: #fff;
    box-sizing: border-box;
}
.admin-info{
    width: 80%;
    font-size: 0;
    margin: 0 auto;
}
.admin-handle{
    width: 100%;
    background: #fff;
    margin-bottom: 20px;
    padding: 20px;
    box-shadow: 0px 4px 12px 0 #dfe2e9;
    box-sizing: border-box;
}
.admin-info-item{
    display: inline-block;
    width: 50%;
    padding: 20px;
    font-size: 14px;
    box-sizing: border-box;
}
.admin-info-item-label{
    display: inline-block;
    width: 100px;
    font-size: 14px;
}
.admin-info-item-content{
    display: inline-block;
    width: clac(100% - 80px);
    font-size: 14px;
}
.header-right{
    float: right;
    padding-right: 20px;
}
.table-title-content{
    padding-top: 10px;
}
.table-title-content-item{
    line-height: 24px;
}
.font-nowrap{
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
</style>



