<template>
    <div>
        <v-head :name='"商品列表"'></v-head>
        <div class="module-content">
            <div class="content-search">
                 <el-button type="primary" @click='add'>新增商品</el-button>
            </div>
            <div class="accout-table">
                <el-table :data="commodityList" style="width: 100%">
                    <el-table-column prop="goodsName" label="商品名称" width="180"></el-table-column>
                    <el-table-column prop="goodsPrice" label="商品价格" ></el-table-column>
                    <el-table-column prop="goodsCounts" label="商品数量" :show-overflow-tooltip="true"></el-table-column>
                   <el-table-column fixed="right" label="操作" >
                         <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <add-goods @close='addGoodsColse' :show='addGoodsShow' v-if='addGoodsShow'></add-goods>
    </div>
</template>

<script>
import headers from "@/components/header"
import addGoods from "./dialog/addGoods"
import {gerGoods,deleteGoods} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "commodity",
    components: {
        "v-head": headers,
        "add-goods": addGoods,
    },
    data: function(){
        return {
            commodityList: [],
            pageNumber: 1,
            pageSize: 10,
            addGoodsShow: false
        }
    },
    mounted: function(){
        this.getCommodity()
    },
    methods: {
        add: function(){
            this.addGoodsShow = true;
        },
        getCommodity: function(){
            let data = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize
            }
            gerGoods(data).then(res => {
                if(res.data.code === 0){
                    this.commodityList = res.data.data || []
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        addGoodsColse: function(){
            this.addGoodsShow = false;
            this.getCommodity();
        },
        handleClick: function(val){
            this.$confirm('确认删除？')
                .then(_ => {
                   this.deleteData(val)
            })
            .catch(_ => {});
        },
        deleteData: function(val){
            deleteGoods(val.goodsId).then(res => {
                if(res.data.code == 0){
                    this.$message({
                        message: "删除成功！",
                        type: "success"
                    });
                    this.getCommodity()
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        }
    }
}
</script>

<style>
.content-search{
    padding: 20px 0;
    margin-bottom: 10px;
}
</style>



