<template>
    <div>
        <el-table :data='bac003Info'>
            <el-table-column prop="txHash" label="交易哈希" :show-overflow-tooltip="true" width="480"></el-table-column>
            <el-table-column prop="contractAddress" label="合约地址" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="_initialSupply" label="发行总量" ></el-table-column>
            <el-table-column prop="_uri" label="资产描述" ></el-table-column>
            <el-table-column prop="_data" label="备注" ></el-table-column>
            <el-table-column prop="blockTimeStamp" label="创建时间" :show-overflow-tooltip="true" ></el-table-column>
        </el-table>
    </div>
</template>
<script>
import {getBCA003Info} from "@/api/api"
export default {
    name: "bacDetail",
    props: ['address'],
    data: function(){
        return {
            bac003Info: [],
            contractAddress: this.address
        }
    },
    mounted: function(){
        this.getbac003Info()
    },
    methods: {
        getbac003Info: function(){
            this.bac003Info = []
            let reqData = {
                pageNumber: 1,
                pageSize: 100,
                contractAddress: this.contractAddress
            }
            getBCA003Info(reqData).then(res => {
                if(res.data.status === 0){
                    // this.BAC003Total = res.data.data.totalCount;
                    this.bac003Info = res.data.data.result
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
    }
}
</script>







