<template>
    <div>
        <el-dialog title="新增帐号" :visible.sync="dialogVisible" :before-close="modelClose" class="dialog-wrapper" width="400px">
            <el-form :model="accout" :rules="rules" ref="accoutForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="帐号名称" prop="name">
                    <el-input v-model="accout.name"></el-input>
                </el-form-item>
                <el-form-item label="帐号描述">
                    <el-input v-model="accout.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modelClose">取 消</el-button>
                <el-button type="primary" @click="submit('accoutForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {addAccount} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "addAccount",
    props: ['show'],
    data: function(){
        return {
            dialogVisible: this.show || false,
            accout: {
                name: "",
                description: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "请输入合约名称",
                        trigger: "blur"
                    },
                    {
                        min: 1,
                        max: 32,
                        message: "长度在 1 到 32 个字符",
                        trigger: "blur"
                    },
                ]
            }
        }
    },
    methods: {
        modelClose: function(){
            this.$emit("close")
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.add()
                }else{
                    return false;
                }
            })
        },
        add: function(){
            let data = {
                accountName: this.accout.name,
                description: this.accout.description
            }
            addAccount(data).then(res => {
                if(res.data.code === 0){
                    this.$message({
                        message: "新增帐号成功！",
                        type: "success"
                    });
                    this.modelClose()
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                    this.modelClose()
                }
            }).catch(err => {
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
                this.modelClose()
            })
        }
    }
}
</script>

<style>

</style>



