/**Get request parameter processing
 * @param necessary Required
 * @param query Optional
 * @return {Object}
 */
export function reviseParam(necessary, query) {
    let params = arguments[0],
        querys = arguments[1],
        arr = [],
        str = '';
    for (var i in params) {
        arr.push(params[i])
    }
    str = arr.join('/');
    
    return { str, querys }
}


