<template>
    <div>
        <el-dialog title="添加权限" :visible.sync="dialogVisible" :before-close="modelClose" class="dialog-wrapper" width="400px">
            <el-form :model="accout" :rules="rules" ref="accoutForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="帐号名称" prop='accountId'>
                    <!-- <el-input v-model="accout.accountId"></el-input> -->
                    <el-autocomplete v-model="accout.accountId" :fetch-suggestions="querySearchAsync"></el-autocomplete>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modelClose">取 消</el-button>
                <el-button type="primary" @click="submit('accoutForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import {addAdministration,getAccountList} from "@/api/api"
import errorCode from "@/util/errorCode"
export default {
    name: "addAccount",
    props: ["show",'id'],
    data: function(){
        return {
            dialogVisible: this.show,
            accout: {
               accountId: null 
            },
            assetId: this.id,
            accoutData: [],
            rules: {
                accountId: []
            }
        }
    },
    mounted: function(){
        this.getAccouts()
    },
    methods: {
        getAccouts: function(){
            getAccountList().then(res => {
                if(res.data.code === 0){
                    this.accoutData = res.data.data
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        querySearchAsync(queryString, cb) {
            var restaurants = this.accoutData;
            for(let i = 0; i < restaurants.length; i++){
                restaurants[i].value = restaurants[i].accountName
            }
            var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;

            clearTimeout(this.timeout);
            this.timeout = setTimeout(() => {
                cb(results);
            }, 500 * Math.random());
        },
        createStateFilter(queryString) {
            return (state) => {
                return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        modelClose: function(){
            this.$emit("close")
        },
        submit: function(formName){
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.add()
                }else{
                    return false;
                }
            })
        },
        add: function(){
            let accountId = null;
            for(let i = 0; i < this.accoutData.length; i++){
                if(this.accoutData[i].accountName == this.accout.accountId){
                    accountId = this.accoutData[i].accountId
                }
            }
            let data = {
                assetId: this.assetId,
                accountType: 0,
                accountId: accountId
            };
            addAdministration(data).then(res => {
                if(res.data.code === 0){
                    this.$message({
                        message: "增加权限成功！",
                        type: "success"
                    });
                    this.modelClose()
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })

        }
    }
}
</script>

<style>

</style>



