<template>
    <div style="width: 100%">
        <v-head :name='"资产概览"'></v-head>
        <div class="module-content">
            <div class="overview-content">
                <div class="overview-content-item" @click='changeBAC("BAC001")'>
                    <div class="overview-content-item-title">BAC001</div>
                    <p>份额型资产</p>
                    <p class="overview-content-item-number">{{total1}}</p>
                </div>
                <div class="overview-content-item" @click='changeBAC("BAC002")'>
                    <div class="overview-content-item-title">BAC002</div>
                    <p>唯一型资产</p>
                    <p class="overview-content-item-number">{{total2}}</p>
                </div>
                <div class="overview-content-item" @click='changeBAC("BAC003")'>
                    <div class="overview-content-item-title">BAC003</div>
                    <p>批量资产</p>
                    <p class="overview-content-item-number">{{BAC003Total}}</p>
                </div>
            </div>
            <div class="overview-table" style="width: 100%" v-if='!BAC003Show'>
                <div>
                    <h3>{{bacType}}资产列表</h3>
                </div>
                <el-table :data="list" style="width: 100%" key='bac001bac002'>
                    <el-table-column prop="txHash" label="交易哈希" :show-overflow-tooltip="true" width="480"></el-table-column>
                    <el-table-column prop="assetName" label="资产名称" >
                        <template slot-scope="scope">
                            <el-button @click="handleClick(scope.row)" type="text" size="small">{{scope.row.shortName}}</el-button>
                        </template>
                    </el-table-column>
                   
                    <el-table-column prop="contractAddress" label="合约地址" :show-overflow-tooltip="true" width="320"></el-table-column>
                    <el-table-column prop="totalAmount" label="初始发行总量" v-if='bacType === "BAC001"'></el-table-column>
                    <el-table-column prop="minUnit" label="资产粒度" v-if='bacType === "BAC001"'></el-table-column>
                    <el-table-column prop="description" label="资产描述" ></el-table-column>
                    <el-table-column prop="blockTimeStamp" label="创建时间" :show-overflow-tooltip="true" width="150"></el-table-column>
                </el-table>
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageNumber"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="sizes, prev, pager, next"
                    :total="overViewTotal">
                </el-pagination>
            </div>
            <div class="overview-table" style="width: 100%" v-if='BAC003Show'>
                <div>
                    <h3>{{bacType}}资产列表</h3>
                </div>
                <el-table :data="bac003List" style="width: 100%"  :expand-row-keys="expands" key='bac003' :row-key="getRowKeys">
                    <el-table-column type="expand">
                        <template slot-scope="scope">
                           <v-detail :address="scope.row.contractAddress"></v-detail>
                        </template>
                    </el-table-column>
                    <el-table-column prop="contractAddress" label="合约地址" :show-overflow-tooltip="true" >
                        <template slot-scope="scope">
                            <el-button @click="handleClick003(scope.row)" type="text" size="small">{{scope.row.contractAddress}}</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="blockTimeStamp" label="创建时间" :show-overflow-tooltip="true" width="150"></el-table-column>
                </el-table>
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageNumber"
                    :page-sizes="[10, 20, 30, 50]"
                    :page-size="pageSize"
                    layout="sizes, prev, pager, next"
                    :total="BAC003Total">
                </el-pagination>
            </div>
        </div>
    </div>
    
</template>

<script>
import headers from "@/components/header"
import {getAssetList,getBCA003List,getBCA003Info} from "@/api/api"
import constant from "@/util/constant"
import router from "@/router"
import errorCode from "@/util/errorCode"
import detail from "./dialog/detail"
export default {
    name: 'overview',
    components: {
        "v-head": headers,
        "v-detail":detail
    },
    data: function(){
        return {
            overViewTotal: null,
            total: 0,
            list: [],
            pageNumber: 1,
            pageSize: 10,
            abiData:  constant.ABI,
            bacType: localStorage.getItem("bacType") || "BAC001",
            total1: 0,
            total2: 0,
            BAC003Total: 0,
            BAC003Show: false,
            bac003List: [],
            expands: [],
            bac003Info: [],
            bacTotal: 0,
            getRowKeys: function(row) {
                
                return row.contractAddress;
            }
        }
    },
    mounted: function(){
        this.getList();
        this.getTotal();
        this.getBAC003Data();
        if(this.bacType === 'BAC001'){
            this.abiData = constant.ABI
        }else if(this.bacType === 'BAC002'){
            this.abiData = constant.ABI002
        }else{
            this.abiData = constant.ABI003;
            this.BAC003Show = true
        }
        this.changeABi();
    },
    methods: {
        getChange: function(row){
            this.getbac003Info(row)
        },
        changeABi: function(){
            console.log(Web3)
            let web3 = new Web3(Web3.givenProvider);
            let arry = [];
            if(this.abiData) {
                let list = this.abiData;
                list.forEach(value => {
                    if(value.name && value.type =='function'){
                        let data = {}
                        let methodId = web3.eth.abi.encodeFunctionSignature({
                            name: value.name,
                            type: value.type,
                            inputs: value.inputs
                        });
                        data.methodId = methodId;
                        data.abiInfo = JSON.stringify(value);
                        data.methodType = value.type
                        arry.push(data)
                    }else if(value.name && value.type =='event'){
                        let data = {}
                        let methodId = web3.eth.abi.encodeEventSignature({
                            name: value.name,
                            type: value.type,
                            inputs: value.inputs
                        });
                        data.methodId = methodId;
                        data.abiInfo = JSON.stringify(value);
                        data.methodType = value.type
                        arry.push(data)
                    }
                })
                localStorage.setItem("abiList",JSON.stringify(arry))
            }
        },
        changeBAC: function(row){
            this.bacType = row;
            localStorage.setItem("bacType",row);
            this.changeBacType();
        },
        changeBacType: function(){
            this.BAC003Show = false
            this.bacType = localStorage.getItem("bacType");
            if(this.bacType === 'BAC001'){
                this.abiData = constant.ABI
            }else if(this.bacType === 'BAC002'){
                this.abiData = constant.ABI002
            }else{
                this.abiData = constant.ABI003;
                this.BAC003Show = true
                this.getBAC003Data();
            }
            if(this.bacType !== 'BAC003'){
                this.getList();
            }
            this.changeABi();
        },
        getTotal: function(){
           let data = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize,
                contractName: "BAC001"
            }
            getAssetList(data).then(res => {
                if(res.data.status === 0){
                    this.total1 = res.data.data.totalCount;
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                debugger
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
            let data1 = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize,
                contractName: "BAC002"
            }
            getAssetList(data1).then(res => {
                if(res.data.status === 0){
                    this.total2 = res.data.data.totalCount;
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                debugger
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })

        },
        getBAC003Data: function(){
            this.bac003List = []
            let reqData = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize,
                contractName: "BAC003"
            }
            getBCA003List(reqData).then(res => {
                if(res.data.status === 0){
                    this.BAC003Total = res.data.data.totalCount;
                    this.bac003List = res.data.data.result
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                debugger
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
        getList: function(){
            this.list = [];
            let data = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize
            }
            if(this.bacType != "BAC003"){
                data.contractName = this.bacType
            }else{
                data.contractName = "BAC001"
            }
            getAssetList(data).then(res => {
                if(res.data.status === 0){
                    this.overViewTotal = res.data.data.totalCount;
                    this.list = res.data.data.result
                }else{
                    this.$message({
                        message: errorCode[res.data.status].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                debugger
                this.$message({
                    message: "系统错误！",
                    type: "error"
                });
            })
        },
        handleClick: function(row){
            router.push({
                path: "/administration",
                query: {
                    assetId: row.pkId,
                    total: row.totalAmount,
                    description: row.description,
                    txHash: row.txHash,
                    assetName: row.shortName
                }
            })
        },
        handleClick003: function(row){
            router.push({
                path: "/administration003",
                query: {
                    contractAddress: row.contractAddress
                }
            })
        },
        handleSizeChange: function(){
            this.pageNumber = 1
            this.getList()
        },
        handleCurrentChange: function(){
            this.getList()
        }
    },
    filters: {
        Status: function(value){
            return constant.STATUS[value]
        }
    }
}
</script>

<style>
.overview-content-item{
    display: inline-block;
    margin: 20px 20px 20px 0;
    width: 240px;
    padding: 20px 20px 0 20px;
    background: #fff;
    box-shadow: 0 4px 12px 0 #dfe2e9;
    cursor: pointer;
}
.overview-content-item-title{
    color: #1f83e7;
    font-size: 20px;
    font-weight: bold;
}
.overview-content-item-number{
    padding-left: 20px;
    color:#000;
    font-size: 20px;
    font-weight: bold;

}
.overview-table{
    padding: 5px 20px 10px 5px;
    background: #fff;
    box-shadow: 0 4px 12px 0 #dfe2e9;
}
</style>



