<template>
    <div class="sidebar">
        <div class="sidebar-logo">
            <span>BAC控制台</span>
        </div>
        <div>
            <el-menu default-active="999" router mode="vertical"  
            @select="select"  @open="handleOpen" @close="handleClose" active-text-color="#1f83e7" background-color="#242e42" text-color="#9da2ab">
                <template v-for="(item,index) in routesList" v-if="item.menuShow">
                    <el-menu-item v-if='item.children[0].name !== "资产管理"' :index="item.children[0].path" :style="{'border-left':item.children[0].path == activeRoute ? '3px solid #1f83e7': '',
                                'padding-left':item.children[0].path == activeRoute ? '30px': '33px',}">
                        <span >{{item.children[0].name}}</span>
                    </el-menu-item>
                </template>
            </el-menu>
        </div>
    </div>
</template>

<script>
import {getAssetList} from "@/api/api"
import Bus from "@/bus"
import errorCode from "@/util/errorCode"
export default {
    name: "sidebar",
    data: function(){
        return {
            routesList: [],
            menuShowC: false,
            activeIndex: 0,
            activeRoute: "",
            pageNumber: 1,
            pageSize: 100,
            assetList: []
        }
    },
    mounted: function(){
        this.getRouterList();
        this.getList(this.routerlink)
    },
    methods: {
        select: function(index, indexPath) {
            this.activeIndex = indexPath[0];
            this.activeRoute = index;
        },
        handleOpen: function(){},
        handleClose: function(){},
        getRouterList: function(){
            this.routesList = this.$router.options.routes
        },
        getList: function(callback){
            let data = {
                pageNumber: this.pageNumber,
                pageSize: this.pageSize
            }
            getAssetList(data,{}).then(res => {
                if(res.data.code === 0){
                    this.assetList = res.data.data;
                    if(res.data.data.length){
                        callback(res.data.data[0])
                    }
                }else{
                    this.$message({
                        message: errorCode[res.data.code].cn,
                        type: "error"
                    });
                }
            }).catch(err => {
                this.$message({
                        message: "系统错误！",
                        type: "error"
                    });
            })
        },
        routerlink: function(val){
            Bus.$emit("change",val)
        }
    }
}
</script>

<style>
.sidebar{
    height: 100%;
    background-color: #242e42;
    box-sizing: border-box;
    color: #fff;
}
ul,li{
    list-style: none;
}
.el-menu{
    border-right: none
}
.sidebar-logo{
    padding: 20px 0 40px 0;
    font-size: 20px;
    font-weight: bold;
    text-align: center
}
</style>



